@use "base/variables" as v;

.slider {
	overflow: hidden;
	position: relative;

	&__slide {
		height: 100%;
		flex-shrink: 0;
		width: 100%;
		position: relative;
		transition-property: transform;
	}

	&__imagen {
		width: 100%;
	}

	.swiper-button-next,
	.swiper-button-prev {
		color: v.$blanco;
	}
	.swiper-pagination-bullet {
		background-color: v.$blanco;
		opacity: 0.5;
	}
	.swiper-pagination-bullet-active {
		background-color: v.$primario;
	}
}

.swiper {
	&-slide {
		background-position: center;
		background-size: cover;
	}

	&-slide img {
		display: block;
		width: 100%;
	}
}
