@use "variables" as v;

html {
	font-size: 62.5%;
	box-sizing: border-box;
}
*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	background-color: v.$grisClaro;
	font-family: v.$fuentePrincipal;
	font-size: 1.6rem;
}

p {
	font-size: 2rem;
}
[class$="contenedor"],
.contenedor {
	width: 95%;
	max-width: 1200px;
	margin: 0 auto;
}
img {
	max-width: 100%;
	display: block;
}
ul {
	padding: 0;
	list-style: none;
	margin: 0;
}
.seccion {
	padding: 8rem 0;
}
body::-webkit-scrollbar {
	// background-color: black;
	color: red;
	width: 1rem;
}
body::-webkit-scrollbar-thumb {
	background-color: v.$primario;
	border-radius: 1rem;
	border: 0.1rem solid v.$blanco;
}
