@use "base/mixins" as m;
@use "base/variables" as v;

.cita {
	background-image: url(../img/bg_citas.jpg);
	background-position: center center;
	background-size: cover;
	padding: 5rem 0;
	text-align: center;
	background-attachment: fixed;

	&__heading {
		@include m.heading;
		font-size: 6rem;
		color: v.$blanco;
	}

	&__texto {
		color: v.$blanco;
		max-width: 60rem;
		margin: 0 auto;
		line-height: 2;
	}

	&__enlace {
		@include m.boton(v.$primario, v.$blanco);
		padding: 2rem 10rem;
	}
}
